import { defaultProps } from "../../../api/defaultProps";
import { Table as TableExt } from "@tiptap/extension-table";

export const tablePropSchema = {
  ...defaultProps,
  cols: { default: 3 },
  rows: { default: 3 },
  withHeaderRow: { default: true },
};

const CustomTable = TableExt.extend({
  addAttributes() {
    return {
      ...this.parent?.(),
      cols: {
        default: 3,
        parseHTML: (element) => element.getAttribute("data-cols"),
        renderHTML: (attributes) => {
          return {
            "data-cols": attributes.cols,
          };
        },
      },
      rows: {
        default: 3,
        parseHTML: (element) => element.getAttribute("data-rows"),
        renderHTML: (attributes) => {
          return {
            "data-rows": attributes.rows,
          };
        },
      },
      withHeaderRow: {
        default: true,
        parseHTML: (element) => element.getAttribute("data-with-header-row"),
        renderHTML: (attributes) => {
          return {
            "data-with-header-row": attributes.withHeaderRow ?? true,
          };
        },
      },
      resizable: {
        default: false,
        parseHTML: (element) => element.getAttribute("data-resizable"),
        renderHTML: (attributes) => {
          return {
            "data-resizable": attributes.resizable ?? false,
          };
        },
      },
    };
  },
});

export const Table = {
  node: CustomTable,
  propSchema: tablePropSchema,
};
